import React from 'react';
import { Button, ButtonGroup } from 'reactstrap';
import NumberFormat from "react-number-format";

const RenderSalary = ({ staff }) => {


    return (
        <div key={staff.id} className="col-12 col-md-6 col-lg-4" style={{ margin: "15px 0px" }}>
            <h5>{staff.name}</h5>
            <div style={{ paddingLeft: "25px", margin: "15px 0px" }}>M nhn vin : {staff.id}</div>
            <div style={{ paddingLeft: "25px", margin: "15px 0px" }}>H s lng : {staff.salaryScale}</div>
            <div style={{ paddingLeft: "25px", margin: "15px 0px" }}>S gi lm thm : {staff.overTime}</div>
            <div style={{ paddingLeft: "35px", margin: "15px 0px" }}> <NumberFormat
                value={staff.salary}
                displayType="text"
                thousandSeparator="."
                decimalSeparator=","
                decimalScale={0}

            />   </div>
        </div >

    );

};

function Salary({ salaryList, onSortSalary, onSortSalaryId, onCheckSalary }) {

    return (
        <div className="container" >
            <div className="row text-center">
                <h3 className="col-12">Bng Lng Nhn Vin</h3>
                <div className="col-12">
                    <ButtonGroup style={{ margin: "10px auto" }}>
                        <div style={{ marginLeft: "55px" }}>Sp xp theo M nhn vin :</div>
                        <Button style={{ marginLeft: "35px" }} color={onCheckSalary === "name1" ? "success" : "primary"} onClick={() => onSortSalaryId(1)}>
                            <span className="fa fa-sort-alpha-asc pr-5">
                                M Nhn Vin T Thp n Cao
                            </span>
                        </Button>
                        <Button color={onCheckSalary === "name-1" ? "success" : "primary"} onClick={() => onSortSalaryId(-1)}>
                            <span className="fa fa-sort-alpha-desc pr-5">
                                M Nhn Vin T Cao Xung Thp
                            </span>
                        </Button>
                    </ButtonGroup>
                </div>
                <div className="col-12">
                    <ButtonGroup style={{ margin: "10px auto" }}>
                        <div style={{ margin: "10px 10px" }}>Sp xp theo Lng Nhn Vin :</div>
                        <Button color={onCheckSalary === "salary1" ? "success" : "primary"} onClick={() => onSortSalary(1)}>
                            <span class="fa fa-sort-amount-asc">Lng Nhn Vin T Thp n Cao</span>
                        </Button>
                        <Button color={onCheckSalary === "salary-1" ? "success" : "primary"} onClick={() => onSortSalary(-1)}>
                            <span class="fa fa-sort-amount-desc">Lng Nhn Vin T Cao Xung Thp</span>
                        </Button>
                    </ButtonGroup>
                </div>

            </div>
            <div className="row">
                {salaryList.map((staff) => (
                    <RenderSalary staff={staff} />
                ))}
            </div>
        </div>


    );
}

export default Salary; 


