import React from 'react';
import Moment from 'react-moment';
import {
    Card, CardImg, CardText,
    CardTitle, CardBody, Row, Col, Breadcrumb, BreadcrumbItem
} from 'reactstrap';
import { Link } from 'react-router-dom';

function StaffDetail({ staffDetail }) {

    if (staffDetail != null) {
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/staffs">Nhn Vin</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{staffDetail.name}</BreadcrumbItem>
                    </Breadcrumb>

                </div>
                <Row style={{ margin: "15px 0px" }}>
                    <Col>
                        <Card>
                            <Row className="no-gutters">
                                <Col md="4">
                                    <CardImg
                                        top
                                        width="100%"
                                        src={staffDetail.image}
                                        alt={staffDetail.name}
                                    />
                                </Col>
                                <Col md="8">
                                    <CardBody>
                                        <CardTitle>H v Tn : {staffDetail.name}</CardTitle>

                                        <CardText>
                                            <p>Ngy sinh:  <Moment format="DD/MM/YYYY">
                                                {staffDetail.doB}
                                            </Moment> </p>
                                            <p>Ngy vo cng ty:  <Moment format="DD/MM/YYYY">
                                                {staffDetail.startDate}
                                            </Moment></p>
                                            <p>Phng ban: {staffDetail.department.name}</p>
                                            <p>S ngy ngh cn li: {staffDetail.annualLeave}</p>
                                            <p>S ngy  lm thm: {staffDetail.overTime}</p>
                                        </CardText>

                                    </CardBody>
                                </Col>
                            </Row>
                        </Card>
                    </Col>
                </Row>

            </div>
        );
    } else {
        return (
            <div></div>
        );
    }


}

export default StaffDetail;


